import { ContainerModule } from "inversify";
import * as firebase from "firebase/app";
import "firebase/auth";
import "firebase/firestore";
import firebaseConfig from "../../../config/firebase.config";
const app = firebase.initializeApp(firebaseConfig);
export const symbols = {
    firebaseApp: Symbol.for("firebaseApp"),
    firebaseAuthProvider: Symbol.for("firebaseAuthProvider"),
    firebaseAuth: Symbol.for("firebaseAuth"),
    firestore: Symbol.for("firestore")
};
export default new ContainerModule(bind => {
    bind(symbols.firebaseApp).toConstantValue(app);
    bind(symbols.firebaseAuth).toConstantValue(app.auth());
    bind(symbols.firebaseAuthProvider).toConstantValue(new firebase.auth.GoogleAuthProvider());
    bind(symbols.firestore).toConstantValue(app.firestore());
});
